<template>
  <!-- App.vue -->
  <iApp>
    <iButton slot="headerLeft">L</iButton>
    <iButton slot="headerRight">R</iButton>
    <iSearchField slot="largeHeader"
      width="100%"
      maxlength="15"
      v-model="textValue"
    />
    <Main slot="main"
      :alert="showAlert"
      @onAlert="onAlert"
    />
    <Sub slot="sub" v-if="true"/>
    <iAlert slot="alert"
      title="iAlert"
      @close="showAlert = false"
      v-show="showAlert"
    >
      <div slot="body">{{ textValue }}</div>
      <div slot="footer">
        <iButton bold="true" @click="showAlert = false">Cancel</iButton>
        <iButton @click="showAlert = false">Ok</iButton>
      </div>
    </iAlert>
  </iApp>
</template>

<script>
import {
  iApp,
  iAlert,
  iButton,
  iSearchField
} from 'vue-ios'

import Main from './Main.vue'
import Sub from './Sub.vue'

export default {
  name: 'app',
  components: {
    iApp,
    iAlert,
    iButton,
    iSearchField,
    Main,
    Sub
  },
  data () {
    return {
      textValue: 'Alert body',
      showAlert: false
    }
  },
  created () {
    console.log(iApp)
  },
  methods: {
    onAlert ($event) {
      this.showAlert = $event
    }
  }
}
</script>
